@import "vars";
@import "fonts";
@import "libs";
@import "mixins";

::-webkit-scrollbar {
  margin: 10px;
  width: auto;
  height: 6px;
  padding: 10px;
  background-color: #f5f5f5;
  cursor: pointer;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  width: 6px;
  background-color: $accent;
  cursor: pointer;
  border-radius: 0;
  box-shadow: 0 1px 1px #ffffff inset;
}

::selection {
  background-color: $accent;
  color: white;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1400px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
}

html,
body {
  height: 100%;
}

:active,
:hover,
:focus {
  outline: 0 !important;
  outline-offset: 0 !important;
  box-shadow: none;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 120%;
  font-family: $default-font;
  overflow-x: hidden;
  color: $text;
  background-color: $bg;

  input,
  textarea {
    border: grey 2px solid;
    outline: none;

    &:focus:required:invalid {
      color: $wrong;
      border: $wrong 2px solid;
    }

    &:required:valid {
      border: green 2px solid;
    }
  }
}

main {
  padding-bottom: 100px;
  /* */
}

footer {
  margin-top: -100px;
  /*  */
  height: 100px;
  /* */
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;

  &.img-width {
    width: 100%;
  }
}

b,
strong {
  font-weight: bold;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

button,
input[type="range"] {
  appearance: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: $text;

  &:hover {
    text-decoration: none;
    color: white;
    transition: all 0.25s ease-in-out;
  }
}

svg {
  &.hidden-sprite {
    display: none;
  }
}

.title {
  font-family: $heading;
  text-transform: uppercase;
  letter-spacing: 5px;
  line-height: 100%;
  font-weight: 700;
}

.header {
  color: white;
  width: 100%;
  min-height: 600px;
  height: 92vh;
  position: relative;
  z-index: 1;
  background-color: $bg-header;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.nav {
  display: flex;
  flex-flow: row nowrap;
  max-height: 100px;
  margin-bottom: 30px;
  z-index: 3;
}

.logo__block {
  white-space: nowrap;
  display: flex;
  flex-flow: row nowrap;
  flex-basis: 210px;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  will-change: transform, color;

  svg {
    &:hover {
      transform: scale(1.1);
      transition: all 0.3s linear;
    }
  }

  .logo__text {
    margin-left: 16px;
    font-family: $heading;
    font-size: 25px;
    line-height: 100%;
    font-weight: 700;

    span {
      margin-top: 6px;
      display: block;
      font-family: "Roboto", "Arial", sans-serif;
      font-size: 12px;
      line-height: 100%;
      font-weight: normal;
      color: #788891;
    }

    &:hover {
      color: $accent;
      transition: all 0.3s linear;
    }
  }
}

.header__right {
  display: flex;
  flex-flow: row nowrap;
  flex-basis: 50%;
  justify-content: space-between;
  align-items: center;
}

.header__link {
  color: white;
  font-size: 16px;
  line-height: 100%;
  margin-right: 20px;

  &:hover {
    color: $accent;
    text-decoration: none;
    transform: scale(1.1);
    transition: all 0.4s linear;
  }

  &.header__icon__link {
    transition: all 0.6s linear;
    cursor: pointer;
    will-change: transform;
  }
}

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  white-space: nowrap;
  font-family: $heading;
  font-size: 16px;
  line-height: 100%;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;

  &:active {
    outline: none;
    box-shadow: none;
  }

  &:focus {
    outline: none;
    box-shadow: none;
  }
}

.btn--order {
  font-size: 14px;
  padding: 10px 24px;

  &:hover {
    color: white;
    background-color: $accent;
  }

  &:active {
    box-shadow: 0 1px 2px 0 #0033a1 inset;
    top: 2px;
  }
}

.btn--blue {
  margin-left: 90px;
  font-size: 14px;
  padding: 15px 20px;
  max-width: 406px;
  color: white;
  background-color: $accent;

  &:hover {
    color: $accent;
    background-color: white;
  }

  &:active {
    box-shadow: 0 1px 2px 0 $accent inset;
    top: 2px;
  }
}

.promo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  .promo__img {
    width: 50%;
    z-index: 1;

    &::before {
      content: "";
      background-image: url("../img/plata.png");
      background-repeat: no-repeat;
      background-position: right;
      background-size: contain;
      min-width: 460px;
      min-height: 340px;
      width: 134%;
      height: 373%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }

  .promo__title {
    font-size: 80px;
    color: white;
  }

  .promo__desc {
    padding-left: 90px;
    font-size: 22px;
    line-height: 140%;
    position: relative;
    font-weight: 300;
    margin: 46px 0;

    &::before {
      content: "";
      width: 66px;
      height: 2px;
      background-color: white;
      position: absolute;
      left: 0;
      top: 12px;
    }
  }

  .promo__inner {
    max-width: 55%;
    z-index: 4;
  }
}

.advantages {
  padding-top: 110px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  .advantages__item {
    padding: 44px;
    max-width: 570px;
    border: 1px #ebebeb solid;
    border-left: 2px $accent solid;
    margin-right: 30px;
    margin-bottom: 30px;
    background-color: white;
    will-change: background-color, color, fill;
    cursor: pointer;
    transition: all 0.3s linear;

    .advantages__title {
      font-family: $heading;
      font-size: 24px;
      line-height: 120%;
      font-weight: 400;
      color: $accent;
      transition: all 0.3s linear;
    }

    .advantages__desc {
      font-size: 16px;
      margin-top: 18px;
    }

    &:hover {
      color: white;
      background-color: $accent;
    }
  }

  .advantages__item:hover .advantages__title {
    color: white;
    transform: scale(1.05);
  }

  .advantages__item:hover .icon {
    color: white;
    fill: white;
    transition: all 0.3s linear;
    transform: scale(1.5);
  }
}

.main {
  background-color: white;
  background-image: url("../img/bg-white.png");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}

.about {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  padding-top: 50px;

  .about__title {
    margin-bottom: 35px;
    font-size: 36px;
  }

  .digits {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -2%;
      width: 326px;
      height: 300px;
      background-image: url("../img/2005.png");
      background-repeat: no-repeat;
      background-position: left top;
      background-size: contain;
    }
  }
}

.btn--bordered {
  margin-top: 35px;
  font-size: 14px;
  color: $accent;
  border: 1px solid $accent;
  padding: 12px 36px;
}

#menuToggle {
  display: none;
}

.menu__mobile {
  opacity: 0;
  user-select: none;
  transition: opacity 0.3s linear;
  position: relative;

  .menu {
    right: 2vw;
    width: 300px;
    padding: 30px;
    background-color: gray;
    display: flex;
    flex-flow: column wrap;
    border-radius: 10px;
  }

  .menu__item {
    padding: 10px;

    &:hover {
      background-color: $bg-header;
    }
  }
}

.burger__label {
  user-select: none;
  cursor: pointer;
  width: 37px;
  height: 14px;
  padding-bottom: 18px;
  box-sizing: content-box;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-content: space-between;
  z-index: 5;

  .burger__line {
    &::before {
      content: "";
      position: absolute;
      display: block;
      width: 37px;
      height: 1px;
      background-color: white;
    }
  }
}

.burger__block {
  user-select: none;
  width: 37px;
  height: 14px;
  align-self: center;
  margin-left: auto;
  display: block;
  cursor: pointer;
  margin-right: 20px;
}

#menuToggle:checked ~ .menu__mobile {
  opacity: 1;
  display: block;
}

#menuToggle:checked + .burger__label .burger__line.line1::before {
  transform: rotate(-45deg) translateX(-10px);
  visibility: visible;
  transition: transform 0.3s linear;
}

#menuToggle:checked + .burger__label .burger__line.line2::before {
  transform: rotate(45deg) translateX(-10px);
  visibility: visible;
  transition: transform 0.3s linear;
}

.overlay {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.form__section {
  display: none;
  color: white;
  position: absolute;
  padding: 38px;
  max-width: 490px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $accent;
  z-index: 6;
  overflow: auto;

  legend {
    margin-bottom: 24px;
    font-size: 28px;
    line-height: 140%;
  }

  .btn--bordered {
    width: 100%;
    border: 1px solid white;
    color: white;
    margin-bottom: 34px;

    &:hover {
      background-color: $bg-header;
    }
  }

  .form__desc {
    max-width: 90%;
  }

  .form__input,
  .textarea {
    color: white;
    width: 100%;
    background-color: rgba(white, $alpha: 0.1);
    border-radius: 4px;
    border: none;
    padding: 14px 24px;
    resize: none;
    transition: all 0.3s linear;

    &:hover {
      background-color: $bg-header;
    }
  }

  .checkbox-group {
    width: 90%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    cursor: pointer;
    user-select: none;
    position: relative;

    .checkbox {
      visibility: hidden;
      position: absolute;
    }

    label {
      width: 86%;
      margin-left: auto;
      font-size: 14px;
      line-height: 16px;
      position: relative;
      cursor: pointer;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 16px;
        height: 16px;
        transform: translateX(-52px);
        border: 1px solid white;
        cursor: pointer;
        background: transparent;
      }
    }

    .checkbox:checked ~ label::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 14px;
      height: 14px;
      background-image: url("../img/tick.png");
      background-repeat: no-repeat;
      background-position: 2px;
      transform: translateX(-51px);
      cursor: pointer;
      z-index: 6;
    }
  }

  #close {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 20px;
    height: 20px;
    background-image: url("../img/close.png");
    background-repeat: no-repeat;
    background-color: transparent;
  }
}

.open {
  display: block;
}

::placeholder {
  color: white;
}

@import "media"; // Always at the end
